import React from 'react';
import { Timeline, TimelineItem } from 'vertical-timeline-component-for-react';
const Education = () => {
	return (
		<Timeline lineColor={'#ddd'} className="my-timeline">
			<TimelineItem
				key="001"
				dateText="07/2020 – Present"
				style={{ color: '#e86971' }}
				bodyContainerStyle={{
					background: '#ddd',
					padding: '20px',
					borderRadius: '8px',
					boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)'
				}}
			>
				<h3>B. Tech CSE</h3>
				<h4>Uttaranchal University</h4>
			</TimelineItem>
			<TimelineItem
				key="002"
				dateText="05/2017"
				dateInnerStyle={{ background: '#61b8ff', color: '#000' }}
				bodyContainerStyle={{
					background: '#ddd',
					padding: '20px',
					borderRadius: '8px',
					boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)'
				}}
			>
				<h3>Intermediate Education</h3>
				<h4>DDCMPS Ramnagar, Nainital, UK</h4>
			</TimelineItem>
			<TimelineItem
				key="003"
				dateText="06/2015"
				bodyContainerStyle={{
					background: '#ddd',
					padding: '20px',
					borderRadius: '8px',
					boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)'
				}}
			>
				<h3>High School</h3>
				<h4>DDCMPS Ramnagar, UK</h4>
			</TimelineItem>
		</Timeline>
	);
};

export default Education;
