import React from 'react';
import './Work.css';
import { Timeline, TimelineItem } from 'vertical-timeline-component-for-react';
const Work = () => {
	return (
		<Timeline lineColor={'#ddd'} className="my-timeline">
			<TimelineItem
				key="001"
				dateComponent={ <div></div> }
				style={{ color: '#e86971' }}
				bodyContainerStyle={{
					background: '#ddd',
					padding: '20px',
					borderRadius: '8px',
					boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)'
				}}
			>
				<h3>Java Programming</h3>
				<h4>Insergo Technologies Pvt. Ltd</h4>
			</TimelineItem>
			<TimelineItem
				key="002"
				dateComponent={ <div></div> }
				bodyContainerStyle={{
					background: '#ddd',
					padding: '20px',
					borderRadius: '8px',
					boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)'
				}}
			>
				<h3>IOT using Android</h3>
				<h4>Ensino Research and Development</h4>
			</TimelineItem>
			<TimelineItem
				key="003"
				dateComponent={ <div></div> }
				bodyContainerStyle={{
					background: '#ddd',
					padding: '20px',
					borderRadius: '8px',
					boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)'
				}}
			>
				<h3>IOT using ARDUINO</h3>
				<h4>Cetpa Infotech Pvt. Ltd</h4>
			</TimelineItem>
			<TimelineItem
				key="003"
				dateComponent={ <div></div> }
				bodyContainerStyle={{
					background: '#ddd',
					padding: '20px',
					borderRadius: '8px',
					boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)'
				}}
			>
				<h3>Reservior Modelling And Simulation</h3>
			</TimelineItem>
		</Timeline>
	);
};

export default Work;
