import React from 'react';
import { Timeline, TimelineItem } from 'vertical-timeline-component-for-react';
const Achievements = () => {
	return (
		<Timeline lineColor={'#ddd'} className="my-timeline">
			<TimelineItem
				key="001"
				dateText="2019"
				style={{ color: '#e86971' }}
				bodyContainerStyle={{
					background: '#ddd',
					padding: '20px',
					borderRadius: '8px',
					boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)'
				}}
			>
				<h3>NPTEL-IIT MADRAS</h3>
				<h4>DataBase And Management System</h4>
			</TimelineItem>
			<TimelineItem
				key="002"
				dateText="2020"
				dateInnerStyle={{ background: '#61b8ff', color: '#000' }}
				bodyContainerStyle={{
					background: '#ddd',
					padding: '20px',
					borderRadius: '8px',
					boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)'
				}}
			>
				<h3>NEO-3.0</h3>
				<h4>National Engineering Olympiads</h4>
			</TimelineItem>
			<TimelineItem
				key="003"
				dateText="2019"
				bodyContainerStyle={{
					background: '#ddd',
					padding: '20px',
					borderRadius: '8px',
					boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)'
				}}
			>
				<h3>Skill Universe</h3>
				<h4>Engineering Skill Development Exam</h4>
			</TimelineItem>
			<TimelineItem
				key="003"
				dateText="2020"
				bodyContainerStyle={{
					background: '#ddd',
					padding: '20px',
					borderRadius: '8px',
					boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)'
				}}
			>
				<h3>Taylor's University</h3>
				<h4>Big Data And Artificial Intelligence</h4>
			</TimelineItem>
		</Timeline>
	);
};

export default Achievements
