import React from 'react';
import { Button, Row, Col } from 'react-bootstrap';
const AboutMe = () => {
	return (
		<Row>
			<Col xs={12}>
				<h3>About me</h3>
			</Col>
			<Col xs={12} lg={9}>
				<p>
					Hello! My name is Deepa .I belong to Ramnagar and currently pursing my bachelor's of Tecnology from
					Uttaranchal University,Dehradun. I love to do freelancing in my spare time. I love exploring new
					technologies and often amazed by the progress we as a human species have mad so far in the recent
					years <br />
					I love working with people to do things bigger than I could accomplish alone. I’m motivated by big
					problems, and I think you’ve got some here that I can help solve.<br />
					I am hard working, team-centric, highly driven individual with a clear set of goals <br />
				</p>
			</Col>
			<Col sm={12}>
				<h4>
					I <span role="img">❤️</span> JavaScript, React, GraphQL
				</h4>
				<br />
				<br />
			</Col>
			<Col>
				<h3>Skills</h3><br/>
				<Button variant="success">JavaScript</Button> <Button variant="dark">React</Button>{' '}
				<Button variant="warning">GraphQL</Button> <Button variant="primary">MySQL</Button> <br />
				<br />
				<br />
				<h3> Publications</h3>
				<br />
				<p>Submitted book chapter, <strong>Machine Learning Algorithm for Healthcare Data Analytics</strong></p>
				<br />
				<Button variant="warning">CRC PRESS</Button> <Button variant="secondary">2020</Button> <br />
			</Col>
		</Row>
	);
};

export default AboutMe;
