import React from 'react';
import './SideBar.css';
import { NavLink } from 'react-router-dom';
import { Col } from 'react-bootstrap';
import FacebookIcon from '@material-ui/icons/Facebook';
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import EmailIcon from '@material-ui/icons/Email';
const SideBar = () => {
	return (
		<div className="sidebar">
			<div>
				<Col className="profile-img">
					<img alt="Me!" src={require('../Profile.jpg')} />
				</Col>
				<Col className="profile-about">
					<h1>DEEPA DANGWAL</h1>
					<p><i>@deepadangwal</i></p>
					<p>
						Building Gitconnected.com<br />Final year Student<br />
					</p>
					<hr/>
				</Col>
			</div>
			<div className="sidebar-item">
				<NavLink className="nav-item" activeClassName="active" to="/me">
					Me
				</NavLink>
				<NavLink className="nav-item" activeClassName="active" to="/Achievements">
					Achievements
				</NavLink>
				<NavLink className="nav-item" activeClassName="active" to="/Work">
					Workshop
				</NavLink>
				<NavLink className="nav-item" activeClassName="active" to="/Education">
					Education
				</NavLink>
				<NavLink className="nav-item" activeClassName="active" to="/Project">
					Project
				</NavLink>
			</div>
			<div>
				<Col className="profile-about">
					<p style={{"fontSize": "17px"}}>Get In Touch</p>
					<a href="https://www.facebook.com/profile.php?id=100004817532988" target="_blank"><FacebookIcon className="icon" /></a>
					<a href="https://github.com/Deepadangwal" target="_blank"><GitHubIcon className="icon" /></a>
					<a href="https://www.linkedin.com/in/dangwal-deepa-71051b19b" target="_blank"><LinkedInIcon className="icon" /></a>
					<a href="mailto:dangwaldeepa19@gmail.com" target="_blank"><EmailIcon className="icon" /></a>
					<hr/>
				</Col>
			</div>
		</div>
	);
};

export default SideBar;
